.search-form {
  height: 100px;
  background-color: #ff0;
  justify-content: center;
  align-items: center;
  display: flex;
}

input[type="text"] {
  width: 500px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 8px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  width: 400px;
  height: 400px;
  margin-top: 50px;
  margin-bottom: 100px;
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.info {
  gap: 20px;
  display: flex;
}

.info-item {
  text-align: center;
  width: 80px;
  flex: 1;
  font-size: 15px;
  font-weight: bold;
}

